import Vue from 'vue'
import Router from 'vue-router'
import Eden from '../views/Eden/router' //
import home from '../views/home/router' //
import about from '../views/about/router' //
import page from '../views/page/router'  //
import upload from '../views/upload/router' //
// import notFound from '../views/notFound/App.vue'
Vue.use(Router)

export default new Router({
  mode:'hash', 
  routes: [
    // {
    //   path:'*',
    //   component:notFound
    // },
    {
      path: '/',
      redirect:'/home',
    },
    ...home,
    ...Eden,
    ...about,
    ...page,
    ...upload,
  ],
})


