<template>
	<div class='con'>
		<form id = 'myForm' method = 'post' action='http://localhost:3000/upload' enctype="multipart/form-data" > 
			<label>:<input class='myinput' type="text" name="title"></label>
			<label>:<input class='myinput' type="text" name="introduct"></label>
			<label>:<input class='myinput' type="text" name="type"></label>
			<label>markDown:<input ref = 'file' type="file" name="content"></label>
			<label><input class='button' type="button" name="upload" value="" @click = 'upload'></label>
		</form>
	</div>
</template>

<script type="text/javascript">
	export default {
		data(){
			return{
			}
		},
		methods:{
			upload:function(){
				var formData = new FormData(document.querySelector('#myForm'));
				if($('input[name="content"]')[0].value == '' || undefined){
					alert('');
				}
				else{
					$.ajax({
						url:'/upload',
						type:'POST',
						data:formData,
						dataType:'text',
						processData: false,  // jQuery
					  	contentType: false,  // jQueryContent-Type
						success:function(data){
							alert('success');
						},
					})
				}
			}
		}
	}
</script>

<style lang='less' scoped>
#myForm{
	text-align:left;
	width:600px;
	margin:0 auto;
	padding-top:200px;
	.button{
		width:100px;
		height:30px;
		cursor:pointer;
	}
	label{
		display:block;
		padding:10px;
		.myinput{
			border-bottom:1px solid black;
		}
	}
}
</style>


